.dronebody {
  background-color: #0e0e0e;
  padding-top: 50px;
}

.accredit{
  background-color: #0e0e0e;
}
.drone-engineering-section {
  color: white;
  padding: 50px 10px 55px;
  text-align: center;
  border-radius: 20px;
  position: relative;
  width: 1150px;
  height: 565px;
  margin-top: 0px;
  background-color: rgb(30, 30, 30);
  margin-bottom: 0px;
  margin-left: 50px;
  box-shadow: rgba(0, 0, 0, 0.4);
}

.drone-tag {
  background: black;
  display: inline-flex;
  align-items: center;
  gap: 0px;
  padding: 12px 22px 12px 50px;
  border-radius: 20px;
  font-weight: 500;
  width: 278px;
  height: 37px;
  margin-bottom: 29px;
  margin-top: -15px;
  font-size: 18px;
  font-family: 'Poppins', sans-serif;
  color: white;
  position: relative;
  z-index: 0;
  overflow: hidden;
}

.drone-tag::before,
.drone-tag::after {
  content: "";
  position: absolute;
  left: 0;
  width: 278px;
  height: 1.4px;
  background: linear-gradient(to right, #222222, #c4c3c3, #222222);
  z-index: 1;
}

.drone-tag::before {
  top: 0px;
}

.drone-tag::after {
  bottom: 0;
}

.drone-tag-icon {
  width: 33px;
  height: 33px;
  object-fit: contain;
}

.drone-title {
  font-size: 45px;
  font-weight: 700;
  max-width: 1200px;
  font-family: 'Playfair Display', serif;
  line-height: 120%;
}

.drone-title .highlight {
  font-style: italic;
  color: #ff5c5c;
}

.drone-description {
  margin: 10px 0;
  font-size: 20px;
  font-family: 'Poppins', sans-serif;
  color: #ccc;
}

.drone-cta-buttons {
  display: flex;
  justify-content: center;
  gap: 7px;
  margin: 10px 0 36px 0;
  flex-wrap: wrap;
}

.drone-outline-btn {
  border: 1px solid white;
  background: transparent;
  color: white;
  width: 113px;
  padding: 15px 20px;
  border-radius: 25px;
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
  transition: all 0.3s ease;
}



.drone-start-btn {
  background: #FB614B;
  border: none;
  padding: 15px 28px;
  color: white;
  font-size: 20px;
  width: 216px;
  height: 58px;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  border-radius: 8px;
  margin-top: 10px;
  margin-bottom: 33px;
  cursor: pointer;
  transition: background 0.3s ease;
}

.drone-start-btn:hover {
  background: #e34c4c;
}

.drone-scroll-wrapper {
  width: 1150px;
  height: 36px;
  overflow: hidden;
  background: #000000aa;
  padding: 6px 10px;
  color: #ccc;
  margin-left: -10px;
  font-size: 16px;
  position: relative;
  margin-top: 30px;
  margin-bottom: 40px;
  -webkit-mask-image: linear-gradient(to right, rgb(0, 0, 0) 5%, rgb(0, 0, 0) 94%);
  mask-image: linear-gradient(to right, transparent, rgb(0, 0, 0) 5%, rgb(0, 0, 0) 94%, transparent);

}

.drone-scroll-track {
  display: flex;
  width: max-content;
  animation: drone-scroll-loop 20s linear infinite;
  
}

.drone-scroll-content {
  display: flex;
  white-space: nowrap;
  padding-right: 3px;
}

.drone-scroll-item {
  display: inline-block;
}


@keyframes drone-scroll-loop {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-25%);
  }
}

.drone-accredit-section {
  background-color: #0e0e0e;
  color: #aaa;
  text-align: center;
  width: 1100px;
  padding: 50px 20px 20px 20px;
  font-family: 'Poppins', sans-serif;
  margin-top: -30px;
  margin-bottom: 20px;
  margin-left: 99px;
}

.drone-accredit-heading {
  font-size: 24px;
  font-family: Playfair Display;
  margin-bottom: 15px;
  font-style: italic;
  font-weight: 600;
  font-size:bold;
  color: #BDC5D1;
  align-content: center;
}

.drone-accredit-heading em {
  font-style: italic;
}

.drone-accredit-list {
  display: flex;
  justify-content: center;
  gap: 260px;
  margin-left: -70px;
  flex-wrap: wrap;
}

.drone-accredit-list span {
  font-weight: 600;
  font-size: 24px;
  font-family: Playfair Display;
  color: #9A9A9A;
  opacity: 0.9;
  font-size: bold;
}


@media screen and (max-width: 768px) {
  .drone-title {
    font-size: 1.8rem;
    padding: 0 10px;
  }

  .drone-description {
    font-size: 1rem;
  }

  .drone-cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .drone-outline-btn {
    width: 80%;
    margin-bottom: 10px;
  }

  .drone-start-btn {
    width: 80%;
  }
}


.drone-info-section {
  display: flex;
  justify-content: space-between;
  gap: 60px;
  padding: 10px 70px 30px 70px;
  flex-wrap: wrap;
