}

.drone-testimonial-box {
  display: flex;
  gap: 20px;
  margin-top: 30px;
  align-items: stretch;
  flex-wrap: wrap;
}

.drone-testimonial-person {
  background: linear-gradient(145deg, #141215, #1b1621);
  border-radius: 8px;
  overflow: hidden;
  width: 171px;
  height: 172px;
  text-align: center;
  font-family: 'Poppins', sans-serif;
  color: white;
  box-shadow: 0 0 8px #00000088;
}

.drone-testimonial-person img {
