.drone-built-section {
  margin-top: 50px;
  font-family: 'Poppins', sans-serif;
  color: white;
  position: relative;
  
}

.drone-built-section h2 {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  color: white;
}

.drone-built-section .highlight {
  color: #ff4b4b;
}

.drone-built-carousel {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding: 10px 0;
  mask-image: linear-gradient(to right, transparent 0%, black 15%, black 85%, transparent 100%);
  -webkit-mask-image: linear-gradient(to right, transparent 0%, black 15%, black 85%, transparent 100%);
}

.drone-built-track {
  display: flex;
  gap: 15px;
  animation: droneBuiltScroll 20s linear infinite;
}

.drone-built-card {
  width: 182px;
  height: 182px;
  background: linear-gradient(to right top, rgb(17, 17, 19) 1%, rgb(21, 1, 48) 100%);;
  border-radius: 8px;
  padding: 16px;
  color: #ddd;
  flex: none;
  order: 1;
  flex-grow: 0;
  box-shadow: 0 0 8px rgba(0,0,0,0.3);
  transition: transform 0.3s ease;
}

.built-tag {
  background: linear-gradient(to right, #6F6780 0%,#704433 50%, #322C3C 100%);
  padding: 3px 10px;
  font-size: 12px;
  color: white;
  border-radius: 12px;
  display: inline-block;
  margin-bottom: 10px;
}

.drone-built-card h4 {
  font-size: 15px;
  font-weight: 600;
  color: white;
  margin-bottom: 6px;
  line-height: 1.3;
}

.drone-built-card p {
  font-size: 12px;
  color: #ccc;
  line-height: 1.4;
}

@keyframes droneBuiltScroll {
  0%   { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}

.drone-reviews-section {
  margin-top: 50px;
  color: white;
  font-family: 'Poppins', sans-serif;
}

.drone-review-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #ff4b4b;
}

.drone-review-carousel {
  overflow: hidden;
  width: 100%;
  mask-image: linear-gradient(to right, transparent 0%, black 10%, black 90%, transparent 100%);
  -webkit-mask-image: linear-gradient(to right, transparent 0%, black 10%, black 90%, transparent 100%);
}

.drone-review-track {
  display: flex;
  gap: 20px;
  animation: droneReviewScroll 30s linear infinite;
}

.drone-review-card {
  background: linear-gradient(135deg, #181523, #0f0d15);
  border: 1px solid #333;
  border-radius: 10px;
  padding: 20px;
  min-width: 330px;
  max-width: 330px;
  flex: none;
  color: #eaeaea;
