}

.drone-review-card p {
  font-size: 14px;
  font-style: italic;
  line-height: 1.5;
  color: #ddd;
}

.drone-review-footer {
  display: flex;
  align-items: center;
  margin-top: 15px;
  gap: 10px;
}

.drone-review-footer img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  border: 1px solid white;
}

.drone-review-footer strong {
  color: white;
  font-size: 14px;
}

.drone-review-footer span {
  font-size: 12px;
  color: #aaa;
}

@keyframes droneReviewScroll {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}


